.loginFormMain{
    max-width: 390px;
    position: absolute;
    width: 100%;
    padding: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #F4F8FF;
}
.loginInputDiv{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
}
.loginBtn{
    border-radius: 5px;
    min-width: 100px;
    min-height: 40px;
    background-color: #80C7FF;
    border: none;
}
.loginBtnArea{
    padding:10px;
    position: relative;
    display: flex;
    justify-content: center;
}
.loginFormImgArea{
    position: relative;
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    overflow: hidden;
}
.loginInputBox{
    min-width : 250px;
    z-index: 100;
    min-height: 40px;
    border-radius: 10px;
}
.loginFormImg{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}
.loginFormTitle{
    text-align: center;
    font-size: xx-large;
}
.loginFormTtl{
    font-size: larger;
    text-align: center;
}